* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

h1 {
  padding: 0;
  margin: 3px;
}

.card {
    background: lightblue;
    /* flex-grow: inherit;
    align-self: contain; */
    border-radius: 2px;
    height: 250px;
    height: 20em;
    display: flex;
    align-items: center; 
    margin: 8rem;
    /* margin-top: 10px; */
    position: relative;
    width: 200px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    overflow: visible;
    }

    /* Slide */
    .hover img {
    margin-left: 10px;
    -webkit-transform: scale(1.5);
    transform: scale(1.5);
    transition: .3s ease-in-out;
    }
    
    .hover img:hover {
    margin-left: -20px;
    }
.wrapper {
    background: beige;
    padding-top: 10px;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    /* justify-content: space-around; */
    /* overflow: auto; */
    /* margin-left: auto; */
    /* margin-right: auto; */
    }
    
.gameScore {
    text-align: center;
    height: 70px;
    position: relative;
    line-height: 1em;
    padding: 0;
    margin: 0;
}

div.gameScore {}

h3.score {
    margin-top: 5px;
    padding: 0;
    font-weight: 600;
}

h3.status {
    margin: 0;
    padding: 0;
    color: red;
}
